import barTitle from '@/components/zaiui-common/basics/bar-title';
import _goods_data from '@/static/zaiui/data/goods.js';	//虚拟数据
import _tool from '@/utils/tools.js';	//工具函数
export default {
	components: {
		barTitle,
	},
	data() {
		return {
			bannerCur: 0, bannerList: [], bottomModal: false, modalTitle: '', modalType: 'promotion', selectType: '',
			goodsList: [], interaction: true, barShow: true, TabCur: 0, tabListData: ['宝贝','卖家','互动','推荐']
		}
	},
	onLoad() {
		//this.bannerList = _goods_data.bannerListData();
		this.goodsList = _goods_data.goodsList();
	},
	onReady() {
		_tool.setBarColor(true);
		uni.pageScrollTo({
		    scrollTop: 0,
		    duration: 0
		});
	},
	methods: {
		getttImgUrl(url) {
			return _tool.getttImgUrl(url);
		},
		serviceTap() {
			this.modalTitle = "服务";
			this.modalType = 'service';
			this.showModal();
		},
		showModal() {
			this.bottomModal = true;
		},
		hideModal(e) {
			this.bottomModal = false;
			this.modalTitle = "";
			this.modalType = '';
		},
		setSwiperItem(indexs,index) {
			let max = (index + 1) * 4;
			let min = max - 4;
			let num = indexs + 1;
			if(num > min && num <= max) {
				return true;
			} else {
				return false;
			}
		},
		tabSelect(e) {
			let index = e.currentTarget.dataset.id;
			this.TabCur = index;
			if(index == 0) {
				uni.pageScrollTo({
				    scrollTop: 300,
				    duration: 200
				});
			}
			if(index == 1) {
				uni.pageScrollTo({
				    scrollTop: 1418,
				    duration: 200
				});
			}
			if(index == 2) {
				uni.pageScrollTo({
				    scrollTop: 1723,
				    duration: 200
				});
			}
			if(index == 3) {
				uni.pageScrollTo({
				    scrollTop: 2413,
				    duration: 200
				});
			}
		}
	},
	onPageScroll(e) {
		this.scrollY = e.scrollTop;
		//console.log(e.scrollTop);
		//由于无法获取元素距离顶部的距离，所以，暂时关闭以下功能。
		/*if (this.scrollY < 270) {
			this.TabCur = 0;
			this.barShow = true;
		}
		if (this.scrollY >= 310 && this.scrollY < 1418) {
			this.TabCur = 0;
			this.barShow = false;
		}
		if (this.scrollY >= 1418 && this.scrollY < 1723) {
			this.TabCur = 1;
		}
		if (this.scrollY >= 1723 && this.scrollY < 2413) {
			this.TabCur = 2;
		}
		if (this.scrollY >= 2413) {
			this.TabCur = 3;
		}*/
	},
}
